.wpcf7 {
	label {
		color: #fff;
		font-weight: 900;
		line-height: 1.4;
		letter-spacing: 1.4px;
	}
	.your-name,
	.your-email {
		display: inline-block;
		margin-left: 48px;
		width: calc(100% - 100.2px);
	}

	.wpcf7-submit {
		color: #fff;
	}

	input[type="text"],
	input[type="email"],
	textarea {
		color: #000;
		background-color: #fff;
		width: 100%;
		letter-spacing: 1.4px;
		padding: 20px 15px;
		&[type="text"],
		&[type="email"] {
			border-radius: 0 15px 15px 0;
		}
	}
	textarea {
		min-height: 380px;
		border-radius: 0 0 15px 15px;
		resize: none;
		padding: 20px 15px;
	}

	&__row {
		&:not(:last-of-type) {
			background-color: $color-primary;
			border: solid 6px $color-primary;
			border-radius: 15px;
			& + & {
				margin-top: 20px;
			}
		}
		&:not(:last-of-type):not(:nth-of-type(3)) {
			padding-left: 48px;
		}
		&:last-of-type {
			display: inherit;
			text-align: center;
			margin: 45px auto 0;
		}
	}

	&__textarea-label {
		display: inline-block;
		padding: 20px 0 20px 48px;
	}
}
